import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'rupees'
})
export class RupeesPipe implements PipeTransform {

  transform(value: any, args?: string): any {
    console.log(args);
    if (args){
      var output = value*60;
      return args + " " + output;
    }
    else
    return value * 60;
  }

}



