import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { RouterLink } from '@angular/router';

import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { RegistrationComponent } from './registration/registration.component';
import { ForgotpasswordComponent } from './forgotpassword/forgotpassword.component';
import { ContactusComponent } from './contactus/contactus.component';
import { DataServiceService} from './data-service.service'; 
import { HttpModule} from '@angular/http';
import { MyToggleDirectiveDirective } from './my-toggle-directive.directive';
import { FontDirectiveDirective } from './font-directive.directive';
import { IfDirective } from './if.directive';
import { RupeesPipe } from './rupees.pipe';

const appRoutes: Routes = [
  { path: "log_in", component: LoginComponent },
  { path: "sign_up", component: RegistrationComponent },
  { path: "forgot_password", component: ForgotpasswordComponent },
  { path: "contact_us", component: ContactusComponent },
  { path: "**", component: LoginComponent}
];

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    RegistrationComponent,
    ForgotpasswordComponent,
    ContactusComponent,
    MyToggleDirectiveDirective,
    FontDirectiveDirective,
    IfDirective,
    RupeesPipe
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: true } // <-- debugging purposes only
    ),
    HttpModule
  ],
  providers: [DataServiceService],
  bootstrap: [AppComponent]
})
export class AppModule { }



